ul, li, h3, p {
  margin: 0;
  padding: 0;
}

:root {
  font-size: 62.5%;
}


body {
  font-size: 1.6rem;
  text-align: center;
  background: #424242 url(./images/body_bg.png)
}

button {
  cursor: pointer;
  z-index: 98;
  padding: 3px 8px;
  border-radius: 4px;
  border: 0;
  outline: 0;
  height: 33px;
  background: #232323;
  color: #fff;
  font-family: "Anta", sans-serif;
  text-transform: uppercase;
}

.container {
  height: 40vmin;
  background: #fff url(./images/container_bg.png);
  box-sizing: border-box;
  clear: both;
  display: flex;
  margin: 0 auto;
  max-width: 1280px;
  height: 100vh;
  position: relative;
}

nav {
  width: 230px;
  background: #616161;
  -webkit-box-shadow: 10px 10px 36px -6px rgba(0,0,0,0.2);
  -moz-box-shadow: 10px 10px 36px -6px rgba(0,0,0,0.2);
  box-shadow: 10px 10px 36px -6px rgba(0,0,0,0.2);
}

main {
  flex-grow: 1;
  transition: all 1s;
}

main.show-detail {
  opacity: 1;
  z-index: 100;
}

.masthead {
  display: flex;
  justify-content: flex-end;
  align-content:flex-end;
  width: 100%;
  height: 190px;
  background: #fff url(./images/masthead.jpeg) -50px -494px;
}

.masthead button {
  display: none;
}

@media only screen and (max-width: 1280px){
  .container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0;
  }
  .container main section {
    justify-content: center;
    align-items: center;
  }
  main {
    background: #fff;
    position: absolute;
    top: 0;
    opacity: 0;
  }
  nav {
    width: 100%;
    z-index: 99;
  }
  .masthead button {
    display: block;
  }
}